.btn{
    border-radius: 0;
    i{ display: none; }
}
.btn-primary, .btn-success{
    background-color: #000;
    border-color: #000;
    &:hover{
        background-color: lighten(#000, 20%);
        border-color: lighten(#000, 20%);
    }
}

.btn-danger{
    background-color: $danger;
    border-color: $danger;
    &:hover{
        background: lighten($danger,5%)!important;
        border-color: lighten($danger,5%)!important;
    }
}

.sort_button{
    background-color: transparent;
    border-radius: 4px;
    margin: 0 auto;
    cursor: move;
    display: inline-block;
    vertical-align: top;
    border: 0;
    outline: 0;
    &:before{
        content: "";
        background-image: url('data:image/png;base64,');
        display: inline-block;
        height: 18px;
        opacity: 0.25;
        vertical-align: top;
        width: 22px;
    }
}
.btn-group{ position: static; }
.btn-group .delete_link{
    position: absolute;
    right: -58px;
    width: 58px;
    top: 0;
    bottom: 0;
    background: $danger;
    border-color: $danger;
    color: #fff !important;
    padding: 15px 20px;
    margin: 0;
    display: none;
    &:hover, &:active, &:focus{
        background: darken($danger,5%);
        border-color: darken($danger,15%);
    }

    i{
        display: block;
        font-size: 20px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
    }
}