//Level 1
.skin-just .sidebar-menu>li:hover>a, .skin-just .sidebar-menu>li.active>a{
    background: #1a1a1a;
    color: #FFF;
}
.skin-just .sidebar a{
    color: #f2f2f2;
    i{display: none;}
}

.skin-just .sidebar-menu li{
    border-top: 1px solid #3a3a3a;
}

//Menu level 2
.skin-just .sidebar-menu>li>.treeview-menu{
    background: #333;
    padding: 0;
    a{
        color: #888;padding: 7px 5px 7px 30px;
    }
    li:hover a, li.active a{
        color: #FFF;
        text-decoration: none;
    }
}


